#mars {
    border: 2px solid blue;
    float: right;
    margin-right: -13em;
    margin-top: 2em;
    width: 25%;
  }
  #input1 {
    float: right;
    position:relative;
    right: 90px;
  }
  #btn1 {
    float: right;
    position: relative;
    right: 45px;
  }
  
  
  #mars tr:nth-child(odd) {
    background-color: hsl(120, 80%, 30%);
  }
  #mars tr > td:last-child {
    background-color: white;
  }
  
  #mars tr:nth-child(even) {
    background-color: hsl(120, 80%, 70%);
  }
  
  #mars tr:first-child {
    background-color: hsl(60, 0%, 80%);
  }
  
  #players {
    float: left;
    width: 50%;
    margin-top: 2em;
    border: 3px solid black;
  }
  
  #btn2 {
    margin-bottom: 4px;
    margin-left: -35em;
  }
  
  table > *{
    background-color: hsl(290,59%, 80%);
  }
  
  tr th:last-child {
    background-color: red;
  }
  
  input:last-of-type{
    font-size: 120%;
    margin-top: 0px;
    padding-left: 3px;
  }
  
  code {
    font-size: 125%;
  }
  